interface ButtonCellProps {
  id: number
  info: RefObject<HTMLElement | undefined>
  mobile: boolean
  active:  boolean
  sold:  boolean  
  linked: boolean
  removeOwnerToLinkById: () => void
  setOwnerToLinkById: (owner: number) => void
  toggleCell: (id: number) => void
  children:  React.ReactNode
}


//onMouseOver={() => {setID(id)} } 
export function ButtonCell({ id, info, mobile, active, sold, linked, setOwnerToLinkById, removeOwnerToLinkById, toggleCell, children}: ButtonCellProps ) {
  const idString = (id+1).toString()
  const updateInfo = () => { 
    if (info.current) {
      info.current.innerHTML = idString
    }
    if(sold) {
      setOwnerToLinkById(id)
    }
  }

  return (
      <Cell selected={active} mobile={mobile} sold={sold} linked={linked}
            onMouseEnter={() => {updateInfo()} }
            onMouseLeave={() => {removeOwnerToLinkById()} }
            onClick={()=>{toggleCell(id)} } >
        {children}
      </Cell>
  )
}

